<template>
	<div>
		<Button style="width:120px" @click='showModal=true'>管理商品标签</Button>
		<Modal v-model='showModal' title='商品标签管理' width='600'>
			<div style="max-height:600px;">
				<Button type='primary' @click='handleAdd'>新建标签</Button>
				<Table no-data-text='暂无标签' :columns="columns" :data="tagList" style='margin:20px 0'></Table>
			</div>
			<div slot='footer'></div>
		</Modal>
		<Modal v-model='modal.show' :title='modal.title' @on-visible-change='handleHidden'>
			<Input v-model.trim='modal.name'></Input>
			<div style='margin-top:10px;'>
				<Button type='primary' size='small' @click='handleSubmit'>确定</Button>
				<Button size='small' style='margin-left:10px' @click='modal.show=false'>取消</Button>
			</div>
			<div slot='footer'></div>
		</Modal>
	</div>
</template>
<script>
	export default {
		data() {
			return {
				showModal:false,
				tableLoading:false,
				columns:[],
				tagList:[],
				modal:{
					id:'',
					isAdd:false,
					show:false,
					title:'',
					name:''
				}
			}
		},
		mounted(){
			this.initHeader();
			 this.getTagList();
		},
		methods:{
			getTagList() {
				this.$http.post(this.$api.tag_listall,{}).then(res=>{
			        if(res.data.result=='1') {
			        	this.tagList = res.data.data
			        	this.$emit('list-change',res.data.data.map(it=>it))
			        }
			    }).catch(err=>{
			        console.log(err)
			    })
			},
			handleAdd() {
				this.modal.isAdd = true;
				this.modal.show = true;
				this.modal.title = '新建标签';
				this.modal.id='';
			},
			initHeader() {
				this.columns = [{
					title:'标签名',
					key:'name',
				},{
					title:'操作',
					key:'operate',
					render:(h,params)=> {
						let delBtn = h('Button',{
							props:{
								type:'error',
								size:'small'
							},
							on:{
								click:()=>{

								}
							}
						},'删除');
						let editBtn = h('Button',{
							props:{
								type:'primary',
								size:'small'
							},
							on:{
								click:()=>{
									this.modal.isAdd = false;
									this.modal.show = true;
									this.modal.title = '标签编辑：'+params.row.name;
									this.modal.id = params.row.id;
									this.modal.name = params.row.name;
								}
							}
						},'编辑')
						return h('div',[delBtn,editBtn])
					}
				}]
			},
			handleHidden(show) {
				if(!show) {
					this.modal.isAdd = false;
					this.modal.title = '';
					this.modal.name = '';
					this.modal.id = '';
				}
			},
			handleSubmit() {
				let name = this.modal.name
				if(!name.length) {
					this.$Message.error('标签名不能为空')
					return ;
				}
				let actionPromise;
				if(this.modal.isAdd) {
					actionPromise = this.$http.post(this.$api.tag_add,{name}).then(res=>{
						if(res.data.result=='1') {
							return true
						}else {
							return Promise.reject(res.data.msg)
						}
					},err=>{
						return Promise.reject('网络请求错误')
					})
				}else if(!this.modal.isAdd&&this.modal.id) {
					actionPromise = this.$http.post(this.$api.tag_edit,{id:this.modal.id,name}).then(res=>{
						if(res.data.result=='1') {
							return true
						}else {
							return Promise.reject(res.data.msg)
						}
					},err=>{
						return Promise.reject('网络请求错误')
					})
				}
				if(actionPromise) {
					actionPromise.then(res=>{
						this.$Message.success('保存成功')
						this.modal.show = false;
						this.getTagList()
					}).catch(err=>{
						this.$Message.error(err)
					})
				}
			},
		},
	}
</script>



