<template>
  <div style="margin:20px">
    <Form ref="formValidate" :model="item" :rules="ruleValidate" label-position="left" :label-width="130">
      <FormItem label="商品名称" prop="title">
        <Input v-model="item.title" placeholder="请输入商品名称" size="large"></Input>
      </FormItem>
      <FormItem label="商品类型" prop="itemtype">
        <RadioGroup v-model="item.itemtype">
          <Radio :label="1">产品</Radio>
          <!--<Radio :label="2" style='margin-left:20px;'>服务</Radio>-->
        </RadioGroup>
      </FormItem>
      <!-- <FormItem label="商品分类" prop="cateid">
        <Select v-model="item.cateid" size="large" placeholder="请选择商品分类">
          <Option v-for="cate in categoryList" :value="cate.id" :key="cate.id">{{ cate.catename }}</Option>
        </Select>
      </FormItem> -->
      <FormItem label="市场价格" prop="marketprice">
        <InputNumber v-model="item.marketprice" size="large" :min="0"></InputNumber>
      </FormItem>
      <FormItem label="出售价格" prop="price">
        <InputNumber v-model="item.price" size="large" :min="0"></InputNumber>
        <span style='color:#ccc;margin-left:20px;'>此价格为普通商品价格，活动价请移步营销活动设置</span>
      </FormItem>
      <FormItem label="VIP价格" prop="vipprice">
        <InputNumber v-model="item.vipprice" size="large" disabled></InputNumber>
      </FormItem>
      <FormItem label="库存数量" prop="num">
        <InputNumber v-model="item.num" size="large" :min="0"></InputNumber>
      </FormItem>
      <FormItem label="商品主图" prop="picurl">
        <single-uploader :image='item.picurl' @change='handleImgChange'></single-uploader>
      </FormItem>
      <FormItem label="上架商品" prop="ishidden">
        <i-Switch size="large" v-model="item.ishidden" :true-value='0' :false-value='1'>
          <span slot="open">上架</span> <span slot="close">下架</span>
        </i-Switch>
      </FormItem>
      <FormItem label="首页推荐" prop="isrecommend">
        <i-Switch size="large" v-model="item.isrecommend" :true-value='1' :false-value='0'>
          <span slot="open">是</span> <span slot="close">否</span>
        </i-Switch>
      </FormItem>
      <!-- <FormItem label="商品标签" prop="tags">
        <Select v-model="tags" placeholder="请选择商品标签" size="large" transfer style="width: 400px" multiple>
          <Option v-for="tag in tagList" :value="tag.id" :key="tag.id">{{tag.name}}</Option>
          <div v-if='!tagList.length' style="text-align:center;color:#999">暂无商品标签</div>
        </Select>
        <item-tags style='display:inline-block' @list-change='handleTaglist'></item-tags>
      </FormItem>
     <FormItem label='商品轮播图'>
        <div>
          <swiper-uploader :images='itempicture' @on-del='handleDelPic' @on-add='handleAddPic'
            @on-change='handleChangePic' :max='4'></swiper-uploader>
        </div>
      </FormItem> -->
      <FormItem label="商品详情" prop="description">
        <vue-editor :content='item.description' @change="updateContent"></vue-editor>
      </FormItem>
      <FormItem label="商品sku">
        <sku-table :skudata='itemsku' :itemid='item.id' @sku-change='handleSkuChange'></sku-table>
      </FormItem>
      <FormItem>
        <Button type="primary" @click="handleSubmit('formValidate')" size="large" style="width:120px">保存修改</Button>
        <Button type="ghost" @click="handleReset('formValidate')" size="large" style="margin-left: 8px;width:80px">取消
        </Button>
      </FormItem>
    </Form>
  </div>
</template>
<script>
  import singleImgUploader from '../common/SingleImgUploader.vue'
  import swiperUploader from '../common/MultiImgUploader.vue'
  import skuTable from '../sku/sku-tables.vue'
  import itemTags from './item-tags.vue'

  export default {
    data() {
      return {
        item: {
          id: '',
          title: '',
          cateid: '',
          itemtype: 1,
          marketprice: 0,
          price: 0,
          vipprice: 0,
          oldprice:0,
          num: 0,
          picurl: '',
          description: '',
          isrecommend: 0,
          ishidden: 0,
          itemmodel:0,
          istop:0,
          seckillbegin:'',
          seckillend:''
        },
        ruleValidate: {
          title: [{required: true, message: '标题名称不能为空！', trigger: 'blur'}],
          // cateid: [{type: 'number', required: true, message: '请选择商品分类', trigger: 'change'}],
          itemtype: [{type: 'number', required: true, message: '请选择商品类型', trigger: 'blur'}],
          description: [{required: true, message: '请输入商品介绍', trigger: 'blur'}],
          // picurl: [{required: true, message: '请上传一张商品图片', trigger: 'blur'}],
        },
        categoryList: [],
        itempicture: [],
        itemsku: [],
        tags: [],
        tagList: []
      }
    },
    components: {
      'single-uploader': singleImgUploader,
      'swiper-uploader': swiperUploader,
      'sku-table': skuTable,
      'item-tags': itemTags
    },
    mounted() {
      this.$sitemap.prvname = "商品管理";
      this.$sitemap.currname = '编辑商品';
      this.getDetail();
      console.log("this.$route.query.id",this.$route.query.id)
      // this.getItemDetail(this.$route.query.id)
      
      // this.getCategoryList();
    },
    methods: {
      handleTaglist(list) {
        this.tagList = list;
      },
       getDetail() {
      let that = this;
      let data = { itemid: this.$route.query.id };
      that.$http
        .post(this.$api.item_detail, data)
        .then(res => {
          let resData = res.data.data;
          if (res.data.result == "1" && resData) {
           that.item=resData
          } else {
            that.$Message.error(res.data.msg);
          }
        })
        .catch(err => {
          that.$Message.error("服务器异常");
        });
    },
      //获取商品详情
      getItemDetail(itemid) {
        console.log('itemid',itemid);
        this.$http.post(this.$api.item_detail, {itemid}).then(res => {
          console.log('res',res);
          if (res.data.data) {
            console.log('res.data.data',res.data.data);
            Object.keys(this.item).forEach(key => {
              this.item[key] = res.data.data.item[key];
            });
            this.tags = res.data.data.item.tags.map(it => it.id);
            this.itempicture = res.data.data.itempicture;
            this.itemsku = res.data.data.itemsku;
          }
        })
      },
      //过滤edit的数据
      formatItem(){
        let item={};
        Object.keys(this.item).forEach(key => {
          switch (key){
            case 'seckillbegin':
            case 'seckillend':
              if(!!this.item[key]){
                item[key+'s']=Date.parse(this.item[key])/1000;
              }
              break;
            default:
              item[key]=this.item[key];
              break;
          }
        });
        return item;
      },
      handleSubmit(name) {
        this.$refs[name].validate(valid => {
          if (valid) {
            this.confirm('您对商品的修改将会被保存,请确认保存', '提示').then(() => {
              this.postItemEdit(this.formatItem()).then(res => {
                // let thePromise = Promise.resolve(true)
                // let tagids = this.tags.join(',');
                // thePromise = thePromise.then(() => {
                //   return this.$http.post(this.$api.itemtag_add, {itemid: this.item.id, tagids}).then(res => {
                //     console.log(res.data)
                //   })
                // })
                if (res.data.result == '1') {
                  // this.getItemDetail(this.$route.query.id)
                  this.$router.push('/item/list')
                  this.$Message.success('保存成功！')
                } else {
                  this.$Message.error(res.data.msg)
                }
              }).catch(error => {
                this.$Message.error('保存失败！')
              })
            }).catch(() => {
            })
          } else {
            this.$Message.error('请输入正确的格式')
          }
        })
        // this.confirm('对商品的修改将会被保存(商品sku的修改需要单独保存),请确认保存', '提示').then(() => {
        //   this.postItemEdit(this.item).then(res => {
        //     this.getItemDetail(this.$route.query.id)
        //     this.$Message.success('保存成功！')
        //   }).catch(error => {
        //     this.$Message.error('保存失败！')
        //   })
        // }).catch(() => {})
      },

      handleReset(name) {
        this.$router.push('/item/list')
      },

      //description 变化
      updateContent(content) {
        this.item.description = content
      },
      //提交商品编辑
      postItemEdit(params) {
        return this.$http.post(this.$api.item_edit, Object.assign({}, params, {signescape: ['description']}))
     },

      //获取分类列表
      getCategoryList() {
        let that = this;
        let params = {parentid: 0};
        this.$http.post(this.$api.category_list, params).then((res) => {
          that.categoryList = res.data.data ? res.data.data : [];
        }).catch(error => {
          this.$Message.error('error')
        });
      },

      //处理图片变化 主图
      handleImgChange(img) {
        this.item.picurl = img.url
      },

      //处理删除图片 轮播
      handleDelPic(picid) {
        this.$http.post(this.$api.itempicture_del, {itemid: this.item.id, picid}).then(res => {
          return this.getItemSwiperList(this.item.id)
        }).then(() => {
          this.$Message.success('删除成功')
        }).catch(erro => {
          this.$Message.error(erro)
        })
      },

      //处理增加图片 轮播
      handleAddPic(picurl) {
        this.$http.post(this.$api.itempicture_add, {itemid: this.item.id, picurl}).then(res => {
          return this.getItemSwiperList(this.item.id)
        }).then(() => {
          this.$Message.success('添加成功')
        }).catch(erro => {
          this.$Message.error(erro)
        })
      },

      //更换图片 轮播
      handleChangePic(data) {
        this.$http.post(this.$api.itempicture_edit, {
          itemid: this.item.id,
          picid: data.id,
          picurl: data.picurl
        }).then(res => {
          return this.getItemSwiperList(this.item.id)
        }).then(() => {
          this.$Message.success('更换成功')
        }).catch(erro => {
          this.$Message.error(erro)
        })
      },

      //获取轮播图片
      getItemSwiperList(itemid) {
        this.$http.post(this.$api.itempicture_list, {itemid}).then(res => {
          if (res.data.result == '1') {
            this.itempicture = res.data.data
          } else {
            Promise.reject(res.data.msg)
          }
        }).catch(error => {
          this.$Message.error(error)
        })
      },

      confirm(text, title) {
        let that = this
        return new Promise((resolve, reject) => {
          that.$Modal.confirm({
            title: title ? title : '确认',
            content: text,
            onOk: () => {
              return resolve(true)
            },
            onCancel: () => {
              return reject(false)
            }
          })
        })
      },

      handleSkuChange() {
        this.$http.post(this.$api.itemsku_list, {itemid: this.item.id}).then(res => {
          this.itemsku = res.data.data
        })
      },
    }
  }

</script>
<style>
  .demo-upload-list {
    display: inline-block;
    width: 60px;
    height: 60px;
    text-align: center;
    line-height: 60px;
    border: 1px solid transparent;
    border-radius: 4px;
    overflow: hidden;
    background: #fff;
    position: relative;
    box-shadow: 0 1px 1px rgba(0, 0, 0, .2);
    margin-right: 4px;
  }

  .demo-upload-list img {
    width: 100%;
    height: 100%;
  }

  .demo-upload-list-cover {
    display: none;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgba(0, 0, 0, .6);
  }

  .demo-upload-list:hover .demo-upload-list-cover {
    display: block;
  }

  .demo-upload-list-cover i {
    color: #fff;
    font-size: 20px;
    cursor: pointer;
    margin: 0 2px;
  }

</style>



