<template>
  <div>
    <Table border :columns='tableColumns' :data='tableData'></Table>
    <!-- <div class="sku-table-add">
    	<Button type='primary'>添加sku</Button>
    	<number-input :value='testVal' @change='inputValue'></number-input>
    </div> -->
  </div>

</template>
<script>
import NumInput from './number-input.vue'
export default {
  props: {
    skudata: {
      type: Array,
      default () {
        return []
      },
    },
    itemid:{
    	type:null
    },
  },
  data() {
    return {
      tableData: [],
      tableColumns: [],
      testVal:1,
    }
  },
  components:{
  	'number-input':NumInput
  },
  watch: {
    skudata: {
      handler(newval) { 
        this.formatTableHead(newval)
      },
      deep: true
    },
    testVal(newVal) {
    	console.log('值为',newVal)
    }
  },
  mounted() {
  	this.formatTableHead(this.skudata)
  },
  methods: {
  	inputValue(val) {
  		this.testVal = val;
  	},
  	 confirm(text, title) {
      let that = this
      return new Promise((resolve, reject) => {
        that.$Modal.confirm({
          title: title ? title : '确认',
          content: text,
          onOk: () => {
            return resolve(true)
          },
          onCancel: () => {
            return reject(false)
          }
        })
      })
    },
    formatTableHead(src) {
      let attrs = [];
      let tempSku = [];
      src.forEach(sku => {
        let res = {};
        let val = JSON.parse(sku.attrvalue);
        res.id = sku.id;
        res.price = sku.price;
        res.quantity = sku.quantity;
        res = Object.assign({}, res, val)
        Object.keys(val).forEach(attr => {
          if (attrs.indexOf(attr) == -1) {
            attrs.push(attr);
          }
        });
        tempSku.push(res)
      })

      let tempHeader = [{
        key: 'price',
        title: '价格',
        align: 'center',
        render: (h, params) => {
          let self = this
          return h('number-input',{
            props:{
              value:this.tableData[params.index].price
            },
            on:{
              change:val=> {
                this.tableData[params.index].price = val
              },
            }
          })

        },
      
      }, {
        key: 'quantity',
        title: '数量',
        align: 'center',
        render: (h, params) => {
          let self = this;
          return h('number-input',{
            props:{
              value:this.tableData[params.index].quantity
            },
            on:{
              change:val=> {
                this.tableData[params.index].quantity = val
              },
            }
          })
        },
      }, {
      	  title: '操作',
          key: 'action',
          align: 'center',	
          width:150,
          render:(h,params) => {
          	return h('div',[h('Button', {
                props: {
                  type: 'primary',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on: {
                  click: () => {
                    let data = this.tableData[params.index]
                    if(!data.priceError&&!data.quantityError) {
                    	this.confirm('此操作将会改变这条sku信息，请确认保存！','提示').then(()=>{
                    		this.$http.post(this.$api.itemsku_edit,this.transferSubmitData(data)).then(res=>{
                    			this.$Message.success('保存成功！')
                    			this.$emit('sku-change')
                    		}).catch(error=>{
                    			this.$Message.error(''+error)
                    		})
                    	}).catch(()=>{})
                    }
                  }
                }
              }, '保存'),h('Button', {
              	props: {
                  type: 'ghost',
                  size: 'small'
                },
                style: {
                  marginRight: '5px'
                },
                on:{
                	click:()=>{
                		this.confirm('此操作将会删除这条sku信息，请确认保存！','提示').then(()=>{
                			this.$http.post(this.$api.itemsku_del,{itemid:this.itemid,skuid:params.row.id}).then(res=>{
                				this.$Message.success('删除成功！')
                				this.$emit('sku-change')
                			}).catch(error=>{
                				this.$Message.error(''+ error)
                			})
                		}).catch(()=>{})
                	},
                },
              },'删除')])
          }
      }];
      let others = attrs.map(attr => {
        return {
          key: attr,
          title: attr,
          align: 'center'
        }
      })
      this.tableColumns = others.concat(tempHeader)
      this.tableData = tempSku
    },
    transferSubmitData(data) {
    	let res = {};
    	let tempsku = {};
    	let esc = ['price','quantity','priceError','quantityError','id'];
    	res.skuid = data.id;
    	res.itemid = this.itemid;
    	res.price = data.price;
    	res.quantity = data.quantity;

    	Object.keys(data).forEach(key=>{
    		if(esc.indexOf(key)==-1) {
    			tempsku[key] = data[key]
    		}
    	})
    	res.attrvalue = JSON.stringify(tempsku);
    	return res
    },
  },

}

</script>
<style >
.sku-table-add {
	margin:10px 0;
	text-align:right;
	padding-right: 40px;
}
</style>


